body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  background-color: #ddd;

}

div#root {
  max-width: 1200px;
  width: 90%;
  margin: 0 auto;
  font-family: arial;

}

div.main_table {
  padding: 20px;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  -webkit-justify-content: space-between;
          justify-content: space-between;
}

.one_ticket {

  position: relative;
  width: 28%;
  display: -webkit-flex;
  display: flex;
  margin-bottom: 2%;

  -webkit-flex-wrap: wrap;

          flex-wrap: wrap;
  -webkit-align-items: flex-start;
          align-items: flex-start;
  -webkit-justify-content: space-evenly;
          justify-content: space-evenly;

  background-color: #f3dcad;
  border-radius: 20px;
  padding: 10px;
}



.one_ticket div {
  border: 2px solid #bd245f;
  width: 10%;

  display: -webkit-flex;

  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  border-radius: 50%;
  margin: 4%;
  /* font-weight: 700; */
  font-size: 1.12em;

}

.one_ticket div:before {
  content: "";
  display: block;
  padding-top: 100%;
}

.full_number {
  font-weight: bold;
  background-color: #fbb034;
  background-image: linear-gradient(315deg, #fbb034 0%, #ffdd00 74%);
}

@media screen and (max-width: 995px) {
  .one_ticket {
    width: 45%;
  }
}

@media screen and (max-width: 600px) {
  .one_ticket {
    width: 90%;
  }
}
header {

    margin: 0 auto;
    text-align: center;
    background-color: #bd245f;
    color: #fff;
    border-radius: 20px;
    padding: 5px 0;
    margin: 5px 0;
}
.navigation {
    background-color: #e44985;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    -webkit-flex-basis: 30%;
            flex-basis: 30%;
    border-radius: 10px;

}

nav button {
    padding: 10px 20px;

    display: block;
    transition: .2s;
    border: 1px solid #000;
    background: #fff;
}

.navigation span {
    margin: 0 auto;
    font-weight: bold;

}

nav button:hover {
    color: #fff;
    background-color: #000;
}
